  color: #333333;
  min-width: 320px;
  position: relative;
  -webkit-font-smoothing: antialised;
}

body input:focus:required:invalid, body textarea:focus:required:invalid {
  color: red;
  border: 3px solid red;
  border-radius: 3px;
}

body input:focus:required:valid, body textarea:focus:required:valid {
  color: green;
}

.hidden {
  display: none;
}

