 }

/*TAB-вкладки верх*/

.contact_phone {
  text-align: right;
}

.contact_phone label {
  cursor: pointer;
  display: inline-block;
  font-family: "RobotoRegular", sans-serif;
  font-size: 16px;
  color: #ffffff;
  line-height: 45px;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease; 
}

.contact_phone input {
  display: none;
}

.contact_phone input:checked + label {
  text-decoration: underline;
}

.contact_phone input[id="tab-1"]:checked ~ .content .tab-1, .contact_phone input[id="tab-2"]:checked ~ .content .tab-2 {
  display: block;
}

.contact_phone article {
  width: 100%;
  font-family: "RobotoBold", sans-serif;
  font-size: 35px;
  display: none;
}

.top_header h2{
  font-family: "CleanvertisingLight", sans-serif;
  font-size: 37px;
  font-weight: 300;
  margin-top: -25px;
}

/*TAB-вкладка скидки*/

.discount label {
  cursor: pointer;
  display: inline-block;
  font-family: "Robotobold", sans-serif;
  font-size: 16px;
  color: #ffffff;
  line-height: 62px;
  text-transform: uppercase;
  background-color: rgba(255, 255, 255, 0.25);
  padding: 0px 20px;
  margin-right: -4px;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.discount input {
  display: none;
  margin-right: -4px;
}

.discount input:checked + label {
  background-color: rgba(255, 255, 255, 0.6);
  color: #000000;
}

.discount input[id="found-discount"]:checked ~ .content_discount .found-discount, .discount input[id="add-discount"]:checked ~ .content_discount .add-discount {
  display: block;
}

.discount article {
  display: none;
  background-color: rgba(255, 255, 255, 0.6);
  font-family: "RobotoBold",sans-serif;
  font-size: 18px;
  color: #000000;
  text-transform: uppercase;
  line-height: 25px;
}

.discount article .my {
  position: absolute;
  top: 18px;
  left: 0;
  color: #706a67;
}

.found-discount ul, .add-discount ul {
      padding: 25px 0 0 30px;
}

.found-discount ul li, .add-discount ul li {
  list-style-type: none;
  padding-left: 40px;
  position: relative;
  padding-bottom: 10px;
}

.found-discount ul li img, .add-discount ul li img {
  position: absolute;
  top: 8;
  left: 0;
}

